import React from 'react'
import './index.less'
import { Route, Redirect, Switch } from 'react-router-dom'
import Theme from './pages/Theme/index';
import Face from './pages/Face/index';
import About from './pages/About/index';
import NavFooter from './cpns/NavFooter/index';
import SellerIndex from './pages/Seller/index';
import Admin from './pages/Seller/admin/index';
import Palace from "./pages/Palace";


export default class App extends React.Component {

    componentDidMount() {
        // 进入登陆
        if (localStorage.request_count === undefined) {
            console.log('计数为零');
            localStorage.request_count = 0
        }
        if (localStorage.request_count === '0') {
            // 第一次加载就用新用户链接
            console.log('new');
            localStorage.request_count = 1
            document.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx5e44595fadf1d2a2&redirect_uri=https%3a%2f%2fwww.newbrandser.com%2fwechat_redirect&response_type=code&scope=snsapi_userinfo&state=index.html%23%2fabout'
        }
        const cookie2dic = () => {
            var cookies = document.cookie.split("; ")
            var dic = new Array();
            for (let i = 0; i < cookies.length; i++) {
                const e = cookies[i];
                var key_shit = e.split("=")[0]
                var val = unescape(e.split("=")[1])
                if (key_shit === 'nickName') {
                    val = decodeURI(e.split("=")[1])
                }
                dic[key_shit] = val
            }
            console.log(dic);
            return dic
        }
        if (localStorage.request_count !== '0') {
            // 解析cookie成字典
            // 设置用户信息
            localStorage.customer_token  =cookie2dic().token
        }
    }
    render() {
        return (
            <div>
                <div className='main'>
                    <Switch>
                        <Redirect exact from="/" to='/seller' component={SellerIndex} />
                        <Route path="/seller" component={SellerIndex} />
                        <Route path="/admin" component={Admin} />
                        <Route path="/theme" component={Theme} />
                        <Route path="/palace" component={Palace} />
                        <Route path="/face" component={Face} />
                        <Route path="/about" component={About} />
                    </Switch>
                </div>
                {/* 底部 */}
                <NavFooter />
            </div>
        )
    }
}
