import React from 'react'
import './index.less'
import {Spin} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../../../6.7/config";
import {Link} from "react-router-dom";


export default class MyFav extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs:'',
        }
    }

    componentWillMount() {
        console.log(this.state)
        this.getFav()
    }

    getFav = (id) => {
        axios.get(
            baseUrl + '/api/get_collected_product',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((Response) => {
                console.log(Response)
                this.setState({
                    myFavs: Response.data.products,
                });
                console.table('res', this.state);


            })
            .catch((err) => {
                console.log(err);
            })
    };
    renderFav = ()=>{
        let fs = []
        this.state.myFavs.map(f=>
            fs.push(
                <li className='fav-cell'>
                    <Link to={`/seller/${f.id_seller}/p/${f.id}`}>
                    <img src={`${baseUrl}${f.productDatas[0]}`} alt='' />
                        <h2>{f.product_name}</h2>
                        <span className='price'>¥{f.price}</span>
                    </Link>
                </li>
            )
        )
        return fs
    }
    render() {
        return (
            this.state.myFavs!==''
                ?
                <div className='my-favs'>
                    {this.renderFav()}
                </div>
                : <Spin/>

        )

    }
}
